<template>
  <v-layout row justify-center>
    <div>
      <v-dialog v-model="dialogRemoveRpa" persistent :max-width="this.data.size">
        <v-card>
          <v-card-title>
            <span class="headline">{{this.data.nameRpa}}</span>
          </v-card-title>
          <v-card-text>
            <h2 class="title font-weight-light mb-3">Tem certeza que deseja excluir ?</h2>
            <v-flex xs12>
              <v-alert :value="alertShowError" outline type="error">{{this.message}}</v-alert>
              <v-alert :value="alertShowSuccess" outline type="success">{{this.message}}</v-alert>
            </v-flex>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="error" flat @click="close()">Não</v-btn>
            <v-btn color="success" flat @click="deleteRpa()">Sim</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
  </v-layout>
</template>

<script>
import { mapActions } from "vuex";
import router from "@/router";
import { EventBus } from "@/services/event-bus.js";
export default {
  props: {
    data: {
      type: Object,
      default: null
    }
  },
  methods: {
    ...mapActions("rpa", ["deleteRpaUser"]),
    close() {
      this.dialogRemoveRpa = false;
    },
    deleteRpa() {
      const data = {
        id_user: this.data.id_user,
        id_rpa: this.data.id_rpa,
        token: this.data.token
      };
      this.deleteRpaUser(data)
        .then(res => {
          if (res.status == 201) router.push({ name: "Rpa" });
        })
        .catch(() => {
          //erro 500 -> auth expired
          this.alertShowError = true;
          this.message = "Erro ao processar sua requisição.";
        });
    }
  },
  mounted() {
    EventBus.$on("dialogRemoveRpa", event => {
      this.dialogRemoveRpa = event;
    });
  },
  data() {
    return {
      alertShowError: false,
      alertShowSuccess: false,
      message: "",
      dialogRemoveRpa: false
    };
  }
};
</script>

